import tuiDatetime from "@/components/dateTime/dateTime"
export default {
	components: {
		tuiDatetime,
	},
	data() {
		return {
			type: 1,
			startYear: 1980,
			endYear: 2030,
			cancelColor: "#888",
			color: "#5677fc",
			setDateTime: "",
			result: "",
			scrollTop: 0
		}
	},
	methods: {
		show: function() {
			this.cancelColor = "#888";
			this.color = "#5677fc";
			this.setDateTime = "";
			this.startYear = 1980;
			this.endYear = 2030;
			this.type = 3;
			this.$refs.dateTime.show()
		},
		change: function(e) {
			console.log(e)
			this.result = e.result
		},
		navigator(url){
			uni.navigateTo({
				url:url
			})
		}
	},
}
